function getDownload(id, studyId) {
  return `${HOST}/download?id=${id}&studyId=${studyId}`;
}

async function postSurvey(survey) {
  try {

    console.log(survey);

    var appId = survey.appId != null && survey.appId != null ? survey.appId : process.env.VUE_APP_APP_ID;
    var serverUrl = survey.serverUrl != null && survey.serverUrl != null ? survey.serverUrl : process.env.VUE_APP_SERVER_URL;
    var apiKey = survey.apiKey != null && survey.apiKey != null ? survey.apiKey : process.env.VUE_APP_REST_API_KEY;

    const options = {
      headers: {
          'X-Parse-Application-Id': `${appId}`,
          "X-Parse-REST-API-Key": `${apiKey}`,
          "Content-Type": "application/json"
      },            
    }

    //Send submission
    let response = await axios.post(`${serverUrl}/functions/submitConsentAndDemographics`, survey, options);
    if(response.data.result.code != null) {
      console.error("Error", response.data.result.error);
      return false;
    }
    return true;
  } catch (err) {
    console.log(err);
    return false;
  }
}

